'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import React, { MouseEvent, ReactElement, useCallback, useMemo, useState } from 'react';
import { useIntl, FormattedMessage } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { Button } from '@yandex-music-int/sol/components/Button';
import { EntityCard, EntityCardProps, CommonCardControls } from '@yandex-music-int/sol/components/EntityCard';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { ArtistModel, useOnLikeClick } from '@/entities/artist/index.js';

import {
    useCreateFrom,
    useSearchFeedback,
    usePlayableContext,
    WithChildren,
    WithClassName
} from '@/shared/lib/index.js';
import { LikeButton, Link, PlayButton, useFuture } from '@/shared/ui/index.js';

import styles from './ArtistCard.module.scss';

export interface Props extends WithClassName, WithChildren {
    artist: ArtistModel;
    contentLinesCount?: EntityCardProps['contentLinesCount'];
}

export const ArtistCard = observer(({ artist, className, children, contentLinesCount }: Props): ReactElement => {
    const from = useCreateFrom();

    const { isFuture } = useFuture();
    const handleOnLikeClick = useOnLikeClick(artist);
    const { formatMessage } = useIntl();
    const { id, name, coverUri } = artist;

    const artistName = useMemo(() => {
        return formatMessage({ id: 'entity-names.artist-name' }, { artistName: name });
    }, [name, formatMessage]);

    const { isPlaying, togglePlay } = usePlayableContext({
        playContextParams: {
            contextData: {
                type: YaMusicContexts.Artist,
                meta: {
                    id: Number(id)
                },
                from
            },
            loadContextMeta: true
        }
    });

    const { sendLikeSearchFeedback, sendNavigateSearchFeedback, sendPlaySearchFeedback } = useSearchFeedback();

    const handleNavigate = useCallback(() => {
        sendNavigateSearchFeedback?.();
    }, [sendNavigateSearchFeedback]);

    const [onSearchFeedbackPlayed, setOnSearchFeedbackPlayed] = useState(false);
    const handlePlayButtonClick = useCallback(() => {
        if (!onSearchFeedbackPlayed && !isPlaying) {
            setOnSearchFeedbackPlayed(true);
            sendPlaySearchFeedback?.();
        }

        togglePlay();
    }, [isPlaying, onSearchFeedbackPlayed, sendPlaySearchFeedback, togglePlay]);

    const [onSearchFeedbackLiked, setOnSearchFeedbackLiked] = useState(false);
    const handleLikeButtonClick = useCallback(() => {
        if (!onSearchFeedbackLiked && !artist.isLiked) {
            setOnSearchFeedbackLiked(true);
            sendLikeSearchFeedback?.();
        }

        handleOnLikeClick();
    }, [artist.isLiked, handleOnLikeClick, onSearchFeedbackLiked, sendLikeSearchFeedback]);

    //TODO:      MenuButton
    const handleClick = useCallback((event: MouseEvent<HTMLButtonElement>) => {
        event.stopPropagation();
        event.preventDefault();
    }, []);

    const menuControl = useMemo(() => {
        if (isFuture) {
            return (
                <Button
                    //TODO:    ,     
                    aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                    icon={<Icon size="xxs" variant="more" />}
                    size="s"
                    radius="round"
                    onClick={handleClick}
                    className={styles.control}
                />
            );
        }

        return null;
    }, [isFuture, handleClick, formatMessage]);

    const view = useMemo(() => {
        return (
            <Paper className={styles.cover} radius="round" withShadow>
                <Link className={styles.coverLink} href={`/artist/${id}`} aria-label={name} onClick={handleNavigate}>
                    <Image
                        className={styles.image}
                        src={coverUri}
                        size={200}
                        fit="cover"
                        alt={artistName}
                        withAvatarReplace
                    />
                    <CommonCardControls
                        className={styles.controls}
                        variant="artist"
                        playControl={
                            <PlayButton
                                buttonVariant="default"
                                withHover={false}
                                className={clsx(styles.playButton, styles.control)}
                                iconSize="l"
                                variant="filled"
                                onClick={handlePlayButtonClick}
                                isPlaying={isPlaying}
                                disabled={!artist.isAvailableForPlaying}
                            />
                        }
                        likeControl={
                            <LikeButton
                                isLiked={artist.isLiked}
                                onClick={handleLikeButtonClick}
                                variant="default"
                                size="s"
                                iconSize="xxs"
                                className={styles.control}
                            />
                        }
                        menuControl={menuControl}
                    />
                </Link>
            </Paper>
        );
    }, [
        artist.isAvailableForPlaying,
        artist.isLiked,
        artistName,
        coverUri,
        handleLikeButtonClick,
        handleNavigate,
        handlePlayButtonClick,
        id,
        isPlaying,
        menuControl,
        name
    ]);

    return (
        <EntityCard
            className={clsx(styles.root, className)}
            textPosition="center"
            aria-label={artistName}
            title={
                <Caption variant="div" type="entity" size="s" weight="medium" lineClamp={2}>
                    <Link
                        className={styles.titleLink}
                        href={`/artist/${id}`}
                        aria-label={artistName}
                        onClick={handleNavigate}
                    >
                        {name}
                    </Link>
                </Caption>
            }
            description={
                <Caption variant="div" type="entity" size="s" weight="medium">
                    <FormattedMessage id="entity-names.singer" />
                </Caption>
            }
            contentLinesCount={contentLinesCount}
            view={view}
        >
            {children}
        </EntityCard>
    );
});
